function indexCtrl($scope, $http, $log, $rootScope) {
    var swiper = new Swiper('.swiper-container', {
        pagination: '.swiper-pagination',
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        coverflow: {
            rotate: 50,
            stretch: 0,
            depth: 100,
            modifier: 1,
            slideShadows: true
        }
    })
}
function homeCtrl($state) {
    $state.go('home.idx')
}
function idxCtrl($http, $scope,$state) {
    //console.log('sdhf')
    $state.go('home.idx.hot')
    $http({
        url: "../mork/data1.json"
    })
        .success(function (data) {
            console.log(data)
            $scope.scene=data.scene;
            $scope.life=data.life;
            $scope.Beauty=data.Beauty;
            console.log($scope.life)
        })
    //console.log(iscroll)
    //iscroll
    //var wrap = new IScroll('.wrap')
   // console.log(wrap)
    //swiper���� tab�л�
    var swi = new Swiper('.swiper-container', {
        onSlideChangeStart: function (swiper) {
            var idx = swiper.activeIndex;
            $('.nav').children().eq(idx).addClass('bg').siblings().removeClass('bg')
        }
    })
    //����li����ʽҲ���Ըı���ʽ
    $('.nav li').on('click', function () {
        $(this).addClass('active').siblings().removeClass('active');
        swi.slideTo($(this).index())
    })
}
function searchCtrl() {
}
function cammerCtrl() {
}
function userCtrl() {
}
function backCtrl() {
}
function careCtrl() {
}
angular.module('myApp')
    .controller('indexCtrl', indexCtrl)
    .controller('homeCtrl', homeCtrl)
    .controller('idxCtrl', idxCtrl)
    .controller('searchCtrl', searchCtrl)
    .controller('cammerCtrl', cammerCtrl)
    .controller('userCtrl', userCtrl)
    .controller('backCtrl', backCtrl)
    .controller('careCtrl', careCtrl)


