function config($stateProvider,$urlRouterProvider){
    //����Ĭ��ҳ��
    $urlRouterProvider.otherwise('/index')
    $stateProvider
        //����ҳ
        .state('index',{
            url:'/index',
            templateUrl:'views/index.html',
            controller:'indexCtrl'
        })
        //����ҳ��
        .state('home',{
            url:'/home',
            templateUrl:'views/home.html',
            controller:'homeCtrl'
        })
        //����ҳ��Ĭ�ϵ���ҳ
        .state('home.idx',{
            url:'/idx',
            templateUrl:'views/idx.html',
            controller:'idxCtrl'
        })
        //��ҳĬ�ϵ��ȵ�ҳ��
        .state('home.idx.hot',{
            url:'/hot',
            templateUrl:'views/hot.html',
            controller:'idxCtrl'
        })
        //��ҳĬ�ϵĹ�עҳ��
        .state('home.idx.care',{
            url:'/care',
            templateUrl:'views/care.html',
            controller:'careCtrl'
        })
        //����ҳ��������ҳ��
        .state('home.search',{
            url:'/search',
            templateUrl:'views/search.html',
            controller:'searchCtrl'
        })
        //����ҳ����ͼƬ����ҳ
        .state('home.cammer',{
            url:'/cammer',
            templateUrl:'views/cammer.html',
            controller:'cammerCtrl'
        })
        //����ҳ�����û���Ϣҳ
        .state('home.user',{
            url:'/user',
            templateUrl:'views/user.html',
            controller:'userCtrl'
        })
        //����ҳ���ķ����˳�ҳ��ҳ
        .state('home.back',{
            url:'/back',
            templateUrl:'views/back.html',
            controller:'backCtrl'
        })
}
angular.module('myApp')
    .config(config);


